/// <reference path="nodom.ts" />
namespace nodom {
    /**
     * filtera-z
     */
    export class FilterManager {
		/**
		 * 
		 */
		static filterTypes:Map<string,Function> = new Map();
		/**
		 * 
		 */
		static cantEditTypes:Array<string> = ['date','currency','number','tolowercase','touppercase','orderBy','filter'];

        /**
         * 
         * @param name 		
         * @param handler 	{init:foo1,handler:foo2}
         */
        static addType(name, handler) {
            if (!/^[a-zA-Z]+$/.test(name)) {
                throw new NodomError('namedinvalid', TipWords.filterType, name);
            }
            if (this.filterTypes.has(name)) {
                throw new NodomError('exist1', TipWords.filterType, name);
            }
            if (!Util.isFunction(handler)) {
                throw new NodomError('invoke', 'FilterManager.addType', '1', 'Function');
            }
            this.filterTypes.set(name, handler);
        }

        /**
         * 
         * @param name  
         */
        static removeType(name:string) {
            if (this.cantEditTypes.indexOf(name) !== -1) {
                throw new NodomError('notupd', TipWords.system + TipWords.filterType, name);
            }
            if (!this.filterTypes.has(name)) {
                throw new NodomError('notexist1', TipWords.filterType, name);
            }
            this.filterTypes.delete(name);
        }

        /**
         * 
         * @param type 		
         * @return 			true/false
         */
        static hasType(name:string):boolean {
            return this.filterTypes.has(name);
        }

        /**
         * 
         * @param module 	
         * @param type 		
         * @param arguments   0 1 2 3-n
		 * @returns 		
         */
        static exec(module:Module, type:string):string {
            let params = new Array();
            for (let i = 2; i < arguments.length; i++) {
                params.push(arguments[i]);
            }
            if (!FilterManager.filterTypes.has(type)) {
                throw new NodomError('notexist1', TipWords.filterType, type);
            }
            //
            return Util.apply(FilterManager.filterTypes.get(type), module, params);
        }

        /**
         * 
         * @param src 	filtertype:param1:param2:... 	
		 * @returns 	
         */
        static explain(src: string): Array < string > {
            let startStr: string
            let startObj: boolean = false;
            let strings:string = "\"'`"; //
            let splitCh:string = ':'; //
            let retArr = new Array();
            let tmp = ''; //
            for (let i = 0; i < src.length; i++) {
                let ch = src[i];
                //
                if (strings.indexOf(ch) !== -1) {
                    if (ch === startStr) { //
                        startStr = undefined;
                    } else { //
                        startStr = ch;
                    }
                } else if (startStr === undefined) { //
                    if (ch === '}' && startObj) { //
                        startObj = false;
                    } else if (ch === '{') { //
                        startObj = true;
                    }
                }

                //
                if (ch === splitCh && startStr === undefined && !startObj && tmp !== '') {
                    retArr.push(handleObj(tmp));
                    tmp = '';
                    continue;
                }
                tmp += ch;
            }

            //
            if (tmp !== '') {
                retArr.push(handleObj(tmp));
            }
            return retArr;
            /**
             * 
             */
            function handleObj(s:string) {
                s = s.trim();
                if (s.charAt(0) === '{') { //
                    s = eval('(' + s + ')');
                }
                return s;
            }
        }
    }
}