/// <reference path="nodom.ts" />
namespace nodom {
	/**
	 * 
	 */
	export interface IRouteCfg{
		/**
		 * * /:
		 */
		path:string;
		/**
		 * 
		 */
		module?:string|Module;
		/**
		 * 
		 */
		routes?:Array<IRouteCfg>;

		/**
		 * 
		 */
		onEnter?:Function;
		/**
		 * 
		 */
		onLeave?:Function;
		/**
		 * 
		 */
		useParentPath?:boolean;
		/**
		 * 
		 */
		notAdd?:boolean;
		/**
		 * 
		 */
		parent?:Route;
	}
	
	/**
     * routerroute
     * hashroute onEnter()  onLeave()
     * 
     * onLeaveonLeave
     *  /r1/r2/r3   /r1/r4/r5onLeaver3r2
     *  onEnter
	 * @author 		yanglei
     * @since 		1.0.0
     * @date		2017-01-21
     */
    export class Router {
		/**
		 * 
		 */
		static loading:boolean = false;
		/**
		 * map
		 */
		static routes:Map<number,Route> = new Map();
		/**
		 * 
		 */
		static currentPath:string = '';
		/**
		 * useParentPath
		 */
		static showPath:string = '';
		/**
		 * path
		 */
		static waitList:Array<string> = []; 
		/**
		 * index
		 */
		static currentIndex:number = 0;
		/**
		 * 
		 */
		static onDefaultEnter:Function;
		/**
		 * 
		 */
		static onDefaultLeave:Function; 
		/**
		 * module  route {moduleName:routeId,...}
		 */
		static moduleRouteMap:Map<string,number> = new Map();

		/**
		 *  0: 1:element active 2:popstate 
		 */
		static startStyle:number = 0;
	
        /**
         * 
         * @param path 	 
         */
        static addPath(path:string) {
            for (let i = 0; i < this.waitList.length; i++) {
                let li:string = this.waitList[i];
                //
                if (li === path) {
                    return;
                }
                //
                if (li.indexOf(path) === 0 && li.substr(path.length + 1,1) === '/') {
                    return;
                }
            }
            this.waitList.push(path);
            this.load();
        }

        /**
         * 
         */
        static load() {
            //
            if (this.loading || this.waitList.length === 0) {
                return;
            }
            let path = this.waitList.shift();
            this.loading = true;
            this.start(path);
        }

        /**
         * 
         * @param path 	
         */
        static start(path:string) {
            let diff = this.compare(this.currentPath, path);

            //router view
            let parentModule = diff[0] === null ? ModuleFactory.getMain() : ModuleFactory.get(diff[0].module);
            //onleave
            for (let i = diff[1].length - 1; i >= 0; i--) {
                const r = diff[1][i];
                if (!r.module) {
                    continue;
                }
                let module = ModuleFactory.get(r.module);
                if (Util.isFunction(this.onDefaultLeave)) {
                    this.onDefaultLeave(module.model);
                }
                if (Util.isFunction(r.onLeave)) {
                    r.onLeave(module.model);
                }
                //module
                module.unactive();
            }

            let operArr:Array<Function> = []; 	//
            let paramArr:Array<any> = []; 		//
            let showPath:string; 				//

            //active
            if (diff[2].length === 0) { //
                if (diff[0] !== null) {
                    setRouteParamToModel(diff[0]);
                    //
                    if (!diff[0].useParentPath) {
                        showPath = diff[0].fullPath;
                    }
                    diff[0].setLinkActive(true);
                }
            } else { //
                //
                for (let i = 0; i < diff[2].length; i++) {
                    let route = diff[2][i];
                    //
                    if (!route || !route.module) {
                        continue;
                    }

                    if (!route.useParentPath) {
                        showPath = route.fullPath;
                    }

                    if (!parentModule.routerKey) {
                        throw new NodomError('notexist', TipWords.routeView);
                    }

                    //module route map
                    Router.moduleRouteMap[route.module] = route.id;
                    //
                    paramArr.push(route.module);

                    //
                    operArr.push(
                        (resolve, reject, moduleName) => {
                            let module = ModuleFactory.get(moduleName);
                            //before first render 
                            module.addBeforeFirstRenderOperation(function () {
                                //
                                Util.empty(module.container);
                            });
                            //container
                            module.containerParam = {
                                module: parentModule.name,
                                selector: "[key='" + parentModule.routerKey + "']"
                            }

                            //
                            module.active((model) => {
                                let route:Route = Router.routes.get(Router.moduleRouteMap[module.name]);
                                if (!route) {
                                    return;
                                }
                                route.setLinkActive(true);
                                delete Router.moduleRouteMap[module.name];
                                setRouteParamToModel(route);
                                if (Util.isFunction(this.onDefaultEnter)) {
                                    this.onDefaultEnter(model);
                                }
                                if (Util.isFunction(route.onEnter)) {
                                    route.onEnter(model);
                                }
                                parentModule = module;
                                if (resolve) {
                                    resolve();
                                }
                            });
                        }
                    );
                }
            }
            if (!showPath) {
                if (!this.getRoute(path)) {
                    throw new NodomError('notexist1', TipWords.route, path);
                }
            }

            //history popstatehistory
            if (this.startStyle !== 2 && showPath) {
                //state
                if (this.showPath && showPath.indexOf(this.showPath) === 0) {
                    history.replaceState(path, '', Application.routerPrePath + showPath);
                } else { //pushhistory
                    history.pushState(path, '', Application.routerPrePath + showPath);
                }
                //
                this.showPath = showPath;
            }

            if (operArr.length === 0) {
                Router.loading = false;
                Router.startStyle = 0;
                return;
            }

            //currentPath
            this.currentPath = path;

            //
            Linker.gen("dolist", {funcs:operArr, params:paramArr}).then(() => {
                Router.loading = false;
                this.load();
                Router.startStyle = 0;
            }).catch((e) => {
                throw e;
            });

            /**
             * model
             * @param route 	
             */
            function setRouteParamToModel(route:Route) {
                if (!route) {
                    return;
                }
                const module:Module = ModuleFactory.get(route.module);
                let model:Model = module.model;
                let o = {
                    path: route.path
                };
                if (!Util.isEmpty(route.data)) {
                    o['data'] = route.data;
                }
                if (!model) {
                    module.model = new Model({ $route: o }, module);
                } else {
                    model.data['$route'] = o;
                }
                Renderer.add(module);
            }
        }

        /*
         * 
         * @param path 	
         */
        static redirect(path:string) {
            this.addPath(path);
        }

        /**
         * 
         * @param route 	 
         * @param parent 	 
         */
        static addRoute(route:Route, parent:Route) {
            //router tree
            if (RouterTree.add(route, parent) === false) {
                throw new NodomError("exist1", TipWords.route, route.path);
            }

            //map
            this.routes.set(route.id, route);
        }

        /**
         * 
         * @param path 	
         * @param last 	,false
         */
        static getRoute(path:string, last?:boolean):Array<Route> {
            if (!path) {
                return null;
            }

            let routes:Array<Route> = RouterTree.get(path);
            if (routes === null || routes.length === 0) {
                return null;
            }
            //routeid route
            if (last) { //
                return [routes[routes.length - 1]];
            } else { //
                return routes;
            }
        }

        /**
         * 
         * @param path1 	
         * @param path2 	
         * @returns 		[]
         */
        static compare(path1:string, path2:string):Array<any> {
            
            // id
            let arr1:Array<Route> = null;
            let arr2:Array<Route> = null;

            if (path1) {
                arr1 = this.getRoute(path1);
            }
            if (path2) {
                arr2 = this.getRoute(path2);
            }

            let len = 0;
            if (arr1 !== null) {
                len = arr1.length;
            }

            if (arr2 !== null) {
                if (arr2.length < len) {
                    len = arr2.length;
                }
            } else {
                len = 0;
            }

            let retArr1 = [];
            let retArr2 = [];
            let i = 0;

            for (i = 0; i < len; i++) {
                //
                if (arr1[i].id === arr2[i].id) {
                    //
                    if (JSON.stringify(arr1[i].data) !== JSON.stringify(arr2[i].data)) {
                        //i+1
                        i++;
                        break;
                    }
                } else {
                    break;
                }
            }
            //
            if (arr1 !== null) {
                for (let j = i; j < arr1.length; j++) {
                    retArr1.push(arr1[j]);
                }
            }

            //
            if (arr2 !== null) {
                for (let j = i; j < arr2.length; j++) {
                    retArr2.push(arr2[j]);
                }
            }

            //
            let p1:Route = null;
            let p2:Route = null;
            if (arr1 !== null && i > 0) {
                for (let j = i - 1; j >= 0 && (p1 === null || p2 === null); j--) {
                    if (arr1[j].module !== undefined) {
                        if (p1 === null) {
                            p1 = arr1[j];
                        } else if (p2 === null) {
                            p2 = arr1[j];
                        }
                    }
                }
            }
            return [p1, retArr1, retArr2, p2];
        }

        /**
         * active viewview activetrue
         * @param module 	
         * @param path 		viewroute
         */
        static changeActive(module, path) {
            if (!module || !path || path === '' || !module.routerActiveViews) {
                return;
            }
            //router active viewactive class
            module.routerActiveViews.forEach((item) => {
                let dom = module.renderTree.query(item);
                if (!dom) {
                    return;
                }
                // dom route 
                let domPath = dom.props['path'];
                if (dom.exprProps.hasOwnProperty('active')) { // active
                    let model = module.modelFactory.get(dom.modelId);
                    if (!model) {
                        return;
                    }

                    let expr = module.expressionFactory.get(dom.exprProps['active'][0]);
                    if (!expr) {
                        return;
                    }
                    let field = expr.fields[0];
                    //active truefalse
                    if (path === domPath || path.indexOf(domPath + '/') === 0) {
                        model.data[field] = true;
                    } else {
                        model.data[field] = false;
                    }
                } else if (dom.props.hasOwnProperty('active')) { //active
                    //active truefalse
                    if (path === domPath || path.indexOf(domPath + '/') === 0) {
                        dom.props['active'] = true;
                    } else {
                        dom.props['active'] = false;
                    }
                }
            });

        }
    }

	/**
	 * 
	 */
    export class Route {
		/**
		 * id
		 */
		id:number;
		/**
		 * 
		 */
		params:Array<string> = [];
		/**
		 * 
		 */
		data:any = {};
		/**
		 * 
		 */
		children:Array<Route> = [];
		/**
		 * 
		 */
		onEnter:Function;
		/**
		 * 
		 */
		onLeave:Function;
		/**
		 * 
		 */
		useParentPath:boolean;
		/**
		 * 
		 */
		path:string;
		/**
		 * 
		 */
		fullPath:string;
		/**
		 * 
		 */
		module:string;
		/**
		 * 
		 */
		parent:Route;
		/**
		 * 
		 * @param config 
		 */
        constructor(config:IRouteCfg) {
            this.onEnter = config.onEnter;
            this.onLeave = config.onLeave;
            this.useParentPath = config.useParentPath;
            this.path = config.path;
            this.module = config.module instanceof Module ? config.module.name : config.module;

            if (config.path === '') {
                return;
            }

            this.id = Util.genId();

            if (!config.notAdd) {
                Router.addRoute(this, config.parent);
            }

            //
            if (Util.isArray(config.routes)) {
                config.routes.forEach((item) => {
                    item.parent = this;
                    new Route(item);
                });
            }
        }
        /**
         * 
         * @param ancestor 		 true/false
         */
        setLinkActive(ancestor:boolean) {
            let path = this.fullPath;
            let module = ModuleFactory.get(this.module);
            if (module && module.containerParam) {
                let pm = ModuleFactory.get(module.containerParam['module']);
                if (pm) {
                    Router.changeActive(pm, path);
                }
            }
            if (ancestor && this.parent) {
                this.parent.setLinkActive(true);
            }
        }
    }

    /**
     * 
     */
    class RouterTree {
		static root:Route;
        /**
         * route
         *
         * @param route 
         * @return  type Boolean
         */
        static add(route:Route, parent:Route) {
            
            //
            if (!this.root) {
                this.root = new Route({ path: "", notAdd: true });
            }
            let pathArr:Array<string> = route.path.split('/');
            let node:Route = parent || this.root;
            let param:Array<string> = [];
            let paramIndex:number = -1; //
            let prePath:string = ''; //
            for (let i = 0; i < pathArr.length; i++) {
                let v = pathArr[i].trim();
                if (v === '') {
                    pathArr.splice(i--, 1);
                    continue;
                }

                if (v.startsWith(':')) { //
                    if (param.length === 0) {
                        paramIndex = i;
                    }
                    param.push(v.substr(1));
                } else {
                    paramIndex = -1;
                    param = []; //
                    route.path = v; //path
                    let j = 0;
                    for (; j < node.children.length; j++) {
                        let r = node.children[j];
                        if (r.path === v) {
                            node = r;
                            break;
                        }
                    }

                    //
                    if (j === node.children.length) {
                        if (prePath !== '') {
                            node.children.push(new Route({ path: prePath, notAdd: true }));
                            node = node.children[node.children.length - 1];
                        }
                        prePath = v;
                    }
                }

                //
                if (paramIndex === -1) {
                    route.params = [];
                } else {
                    route.params = param;
                }
            }

            //
            if (node !== undefined && node !== route) {
                route.path = prePath;
                node.children.push(route);
            }
            return true;
        }

        /**
         * 
         * @param path  	
         */
        static get(path:string):Array<Route> {
            
            if (!this.root) {
                throw new NodomError("notexist", TipWords.root);
            }
            let pathArr = path.split('/');
            let node = this.root;
            let paramIndex = 0;
            let retArr = [];
            let fullPath = ''; //
            let preNode = this.root; //

            for (let i = 0; i < pathArr.length; i++) {
                let v = pathArr[i].trim();
                if (v === '') {
                    continue;
                }
                let find = false;
                for (let j = 0; j < node.children.length; j++) {
                    if (node.children[j].path === v) {
                        //
                        if (preNode !== this.root) {
                            preNode.fullPath = fullPath;
                            preNode.data = node.data;
                            retArr.push(preNode);
                        }

                        //
                        node = node.children[j];
                        //
                        node.data = {};
                        preNode = node;
                        find = true;
                        break;
                    }
                }
                //
                fullPath += '/' + v;
                //,
                if (!find) {
                    if (paramIndex < node.params.length) { //
                        node.data[node.params[paramIndex++]] = v;
                    }
                }
            }

            //
            if (node !== this.root) {
                node.fullPath = fullPath;
                retArr.push(node);
            }
            return retArr;
        }
    }


    //popstate
    window.addEventListener('popstate', function (e) {
        //statemodule
        const state = history.state;
        if (!state) {
            return;
        }
        Router.startStyle = 2;
        Router.addPath(state);
    });


    /**
     * route
     */
    DirectiveManager.addType('route', {
        init: (directive, dom, module) => {
            let value = directive.value;
            if (Util.isEmpty(value)) {
                return;
            }

            //ahref
            if (dom.tagName === 'A') {
                dom.props['href'] = 'javascript:void(0)';
            }
            let dirObj = {};
            // 
            if (value && value.substr(0, 2) === '{{' && value.substr(value.length - 2, 2) === '}}') {
                let expr = new Expression(value.substring(2, value.length - 2), module);
                dom.exprProps['path'] = expr;
                directive.value = expr;
            } else {
                dom.props['path'] = value;
            }

            //click
            let method = '$nodomGenMethod' + Util.genId();
            module.methodFactory.add(method,
                (e, module, view, dom) => {
                    let path = dom.props['path'];
                    if (!path) {
                        return;
                    }
                    Router.addPath(path);
                }
            );
            dom.events['click'] = new NodomEvent('click', method);
        },

        handle: (directive, dom, module, parent) => {
            //active view 
            if (!module.routerActiveViews) {
                module.routerActiveViews = [];
            }

            if (module.routerActiveViews.indexOf(dom.key) === -1) {
                //
                module.routerActiveViews.push(dom.key);
                if (dom.props.hasOwnProperty('active')) {
                    let route = Router.getRoute(dom.props['path'], true);
                    if (route === null) {
                        return;
                    }
                }
            }

            let path = dom.props['path'];
            if (path === Router.currentPath) {
                return;
            }
            //active
            if (dom.props['active'] && dom.props['active'] !== 'false' && (!Router.currentPath || path.indexOf(Router.currentPath) === 0)) {
                Router.addPath(path);
            }
        }
    });

    /**
     * router
     */
    DirectiveManager.addType('router', {
        init: (directive, dom, module) => {
            module.routerKey = dom.key;
        },
        handle: (directive, dom, module, parent) => {
            return;
        }
    });
}