/// <reference path="nodom.ts" />
namespace nodom{
	/**
	 * 
	 *  jsondom
	 */
	export class Serializer{
		/**
		 *  virtualDomexpressionFactory
		 * @param module 	
		 * @return   		jsonstring
		 */
		static serialize(module:Module){
			let props:Array<string> = ['virtualDom','expressionFactory'];
			let jsonStr:string = '[';

			props.forEach((p,i)=>{
				addClsName(module[p]);
				let s = JSON.stringify(module[p]);
				jsonStr += s;
				if(i<props.length-1){
					jsonStr += ',';
				}else{
					jsonStr += ']'
				}
			});
			
			return jsonStr;

			/**
			 * class name
			 * @param obj 	
			 */
			function addClsName(obj:any){
				if(typeof obj !== 'object'){
					return;
				}

				obj.className = obj.constructor.name;
				
				Util.getOwnProps(obj).forEach((item)=>{
					if(Util.isArray(obj[item])){
						//
						if(obj[item].length === 0){
							delete obj[item];
						}else{
							obj[item].forEach((item1)=>{
								addClsName(item1);
							});	
						}
					}else if(typeof obj[item] === 'object'){
						//
						if(Util.isEmpty(obj[item])){
							delete obj[item];
						}else{
							addClsName(obj[item]);	
						}
					}
				});
			}
		}


		/**
		 * 
		 * @param jsonStr 	json
		 * @param module 	
		 * @returns 		[virtualDom,expressionFactory]	
		 */
		static deserialize(jsonStr:string,module:Module){
			let jsonArr = JSON.parse(jsonStr);
			
			let arr = [];
			let vdom; //dom
			jsonArr.forEach((item)=>{
				arr.push(handleCls(item));
			});

			return arr;

			function handleCls(jsonObj){
				if(!Util.isObject(jsonObj)){
					return jsonObj;
				}

				if(jsonObj.moduleName){
					jsonObj.moduleName = module.name;
				}

				let retObj;
				if(jsonObj.hasOwnProperty('className')){
					const cls:string = jsonObj['className'];
					let param = [];
					//
					switch(cls){
						case 'Directive':
							param = [jsonObj['type'],jsonObj['value'],vdom,module];
							break;
						case 'Event':
							param = [jsonObj['name']];
							break;
					}
					let clazz:any = eval(cls);
					// retObj = new .newInstance(cls,param);
					if(cls === 'Element'){
						vdom = retObj;
					}
					
				}else{
					retObj = {};
				}

				//
				let objArr = [];  //
				let arrArr = [];  //
				Util.getOwnProps(jsonObj).forEach((item)=>{
					//
					if(Util.isObject(jsonObj[item])){
						objArr.push(item);
					}else if(Util.isArray(jsonObj[item])){ //
						arrArr.push(item);
					}else{  //
						if(item !== 'className'){
							retObj[item] = jsonObj[item];	
						}
					}
				});

				//
				objArr.forEach((item)=>{
					retObj[item] = handleCls(jsonObj[item]);
				});

				//
				arrArr.forEach(item=>{
					retObj[item] = [];
					jsonObj[item].forEach((item1)=>{
						retObj[item].push(handleCls(item1));
					});
				});
				return retObj;
			}
		}
	}	
}
